.tic-tac-toe-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #282a36;
  font-family: Arial, sans-serif;

  #gameboard {
    display: none;
  }

  #gameboard.visible {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 10px;
    width: 300px;
    height: 300px;
  }

  #gameboard div {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #44475a;
    padding: 5px;
    color: #ff79c6;
    font-size: 2rem;
    cursor: pointer;
  }

  button {
    display: block;
    margin: 20px auto;
    padding: 10px 20px;
    font-size: 1rem;
    background-color: #bd93f9;
    color: #282a36;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  button:hover {
    filter: contrast(1.5);
  }

  button:active {
    background-color: #ff79c6;
  }

  .active-mod {
    background-color: #ff79c6;
  }

  h1 {
    background: linear-gradient(to right, #ff79c6, #bd93f9);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  h1:hover {
    filter: contrast(1.5);
  }

  #reset {
    display: none;
  }

  #reset.visible {
    display: block;
  }

  #game-menu {
    display: flex;
    gap: 20px;
  }

  #gameboard div.winner {
    color: #50fa7b;
  }

  #gameboard div.tie {
    color: #ffb86c;
  }
}
